"use server";
import { db } from "@/db";
import { redirect } from "next/navigation";
import bcrypt from "bcrypt";
import jwt from "jsonwebtoken";

const JWT_SECRET = process.env.JWT_SECRET || "your-secret-key"; // Ensure to set this in your .env file

export async function createAccount(formData: FormData) {
  try {
    const name = formData.get("name") as string;
    const email = formData.get("email") as string;
    const password = formData.get("password") as string;

    if (!name || !email || !password) {
      throw new Error("Missing required form data");
    }
    const hashedPassword = await bcrypt.hash(password, 10);
    await db.user.create({
      data: { name, email, password: hashedPassword }
    });
    redirect("/login");
  } catch (error) {
    console.error("Error creating account:", error);
    // Handle error appropriately, e.g., send an error response or redirect with error message
  }
}

export async function logIn(formData: FormData) {
  const email = formData.get("email") as string;
  const password = formData.get("password") as string;

  const user = await db.user.findUnique({ where: { email } });
  if (user && await bcrypt.compare(password, user.password)) {
    const token = jwt.sign({ userId: user.id }, JWT_SECRET, { expiresIn: "1h" });
    // Set token in cookies or headers (implementation depends on your authentication strategy)
    redirect(`/user/${user.id}/blocks`);
  } else {
    throw new Error("Invalid email or password");
  }
}

export async function createBlock(formData: FormData) {
  const title = formData.get("title") as string;
  const code = formData.get("code") as string;
  const userId = parseInt(formData.get("userId") as string);

  await db.block.create({ data: { title, code, userId } });
  redirect(`/user/${userId}/blocks`);
}

async function deleteBlock(formData: FormData) {
  const blockId = parseInt(formData.get("blockId") as string);
  await db.block.delete({ where: { id: blockId } });
  redirect("/blocks");
}

async function editBlock(formData: FormData) {
  const blockId = parseInt(formData.get("blockId") as string);
  const title = formData.get("title") as string;
  const code = formData.get("code") as string;
  await db.block.update({ where: { id: blockId }, data: { title, code } });
  redirect("/blocks");
}
